// token
import store from '@/store'
import { Dialog, Toast } from 'vant'
import router from '@/router'

export default {
  methods: {
    loginConfirm () {
      if (store.state.user.userInfo) {
        return true
      } else {
        Dialog.confirm({
          title: '',
          message: '',
          confirmButtonText: '',
          confirmButtonColor: 'red',
          cancelButtonText: ''
        })
          .then(() => {
            router.replace('/login')
          })
          .catch(() => {
            Toast('')
          })
        return false
      }
    }
  }
}
